html, body { height: 100%; }
body { margin: 0; font-family: Roboto, "Helvetica Neue", sans-serif; }



.mdc-text-field:not(.mdc-text-field--invalid):not(.mdc-text-field--focused):not(.mdc-text-field--disabled) {
  .mdc-notched-outline > * {
    border-color:#d7d8db !important;
  }
}

::ng-deep {
    /* Adjust the overall height of the form field */
    .mdc-text-field {
        min-height: 40px; /* Set your desired minimum height */
    }

    /* Adjust the height of the input field */
    .mdc-text-field__input {
        padding-top: 0; /* Remove top padding */
        padding-bottom: 0; /* Remove bottom padding */
        height: auto; /* Ensure height adjusts */
    }

    /* Adjust the label line height */
    .mdc-floating-label {
        line-height: 1; /* Adjust line height if necessary */
    }

    /* Adjust the height of the notched outline */
    .mdc-notched-outline {
        height: 40px; /* Set your desired height for the outline */
    }

    /* Adjust the select field */
    .mdc-select {
        height: 40px; /* Set your desired height for mat-select */
    }

    /* Optional: Adjust padding for select options */
    .mdc-select .mdc-select__menu {
        padding: 0; /* Remove padding if necessary */
    }
}

.mat-mdc-form-field-infix {
    padding: 8px 0 !important;
}

.mdc-text-field--outlined .mdc-notched-outline {
    padding: 0px 0;
    height: 40px;
}


  
.mat-mdc-form-field .mat-mdc-floating-label.mdc-floating-label{
  font-size: 14px;
}

.mat-mdc-select{
  font-size: 14px !important;
  margin: 5px 0 0 0;
}

	
.mat-mdc-form-field:not(.mat-form-field-disabled) .mat-mdc-floating-label.mdc-floating-label {
  margin: 1px 0 0 0;
  height: 100%;
}


