.nav-panel {
  ul {
    background-color: #fff;
    padding: 0px !important;
    margin: 0;
    list-style: none;
    hr {
      background-color: rgba(0, 0, 0, 0.15);
      height: 1.2px;
      border: 0;
      margin-top: 2px !important;
      margin-bottom: 2px !important;
    }
    li {
      user-select: none;
      color: rgba(0, 0, 0, 0.9);
      padding: 0 24px !important;
      font-weight: 500 !important;
      //margin-right: 10px !important;
      //border-top-right-radius: 24px !important;
      //border-bottom-right-radius: 24px !important;
      font-weight: 400;
      font-size: 14px;
      cursor: pointer;
      height: 40px;
      display: flex;
      align-items: center;
      margin: 0;
      i {
        color: rgba(0, 0, 0, 0.6);
        padding-right: 32px;
      }
    }
    li:focus {
      outline: none !important;
    }
    li:hover {
      background-color: #f2f2f2 !important;
    }
  }
}

.nav-heading {
  color: #1a75ff !important;
}
.vertical-nav-link {
  text-decoration: none !important;
  padding: 0px !important;
  margin: 0px !important;
  color: #333333 !important;
  font-family: 'HK-Grotesk-SemiBold' !important;
}
.vertical-nav-link-active {
  font-weight: 500 !important;
  // background: linear-gradient(45deg, #303f9f, #7b1fa2) !important;
  // box-shadow: 3px 3px 12px 0 rgba(123, 31, 162, .5) !important;
  position: relative;
  border-left: 4px solid #1a75ff !important;
  background-color: #e6f0ff !important;
  font-family: 'HK-Grotesk-SemiBold' !important;
}
.vertical-nav-link-active a {
  color: #1a75ff !important;
  //color: white !important;
}
.nav-panel { border-bottom: 0.2px solid #eceaea !important; padding-bottom: 0px !important; }

.nav-panel-main { border-bottom: 0.8px solid #d9d9d9 !important; padding-bottom: 0px !important; }

